import React,{useRef} from 'react';
import ReactDOM from 'react-dom'
import Header from '../Header'
import About from '../About'
import Projects from '../Projects'
import ProjectDetails from '../ProjectDetails'
import Cursor from '../Cursor'
import {BrowserRouter,Route,Switch,Redirect} from 'react-router-dom'
import styled from 'styled-components';
import '../../main.css';
import{TransitionGroup,CSSTransition} from'react-transition-group'
import {Transition} from 'react-spring/renderprops'
import TransitionBlock from '../Transition'

const Wrapper = styled.div`
  width:100%;
  margin:0 auto;
  overflow:hidden;
`
function App() {
  let transitionDom=useRef(null)
  function handleEnter(target){
    if(transitionDom.current !=null){
      transitionDom.current.domElement.current.style.opacity=target
    }
  }

  return (
    <Transition
      items={[1]}
      config={{duration:500}}
      from={{ opacity: 0 }}
      enter={{ opacity: 1 }}
      leave={{ opacity: 0 }}>
      {show => show && (props => 
        <Wrapper style={props}>
          <Cursor></Cursor>
          <TransitionBlock ref={transitionDom} />
          <BrowserRouter  basename={process.env.PUBLIC_URL}>
              <Header></Header>
              <Route render={(location) => {
                location = location.location
                const { key } = location
                return(
                  <TransitionGroup>
                    <CSSTransition
                    onEnter={()=>{handleEnter(1)}}
                    onEntered={()=>{handleEnter(0)}}
                    key={key}
                    classNames="page"
                    timeout={600}
                    >
                      <Switch location={location}>
                        <Route exact path="/">
                          <Redirect to="/projects/"></Redirect>
                        </Route>
                        <Route exact path="/projects/" component={Projects}></Route>
                        <Route path="/about" component={About}></Route>
                        <Route path="/projects/:url" component={ProjectDetails}></Route>
                      </Switch>
                    </CSSTransition>
                  </TransitionGroup>
                )
              }}/>
          </BrowserRouter>
      </Wrapper>
      )}
    </Transition>
  );
}

export default App;
