import React from 'react'
import { useParams } from "react-router"
import styled from 'styled-components'
import data from '../../assets/data.js'
import {StyledBtn,StyledIconLink,PageWrapper} from '../shared_components.js'
import theme from '../../assets/theme.js'

const Wrapper = styled.div`
  margin-left:auto;
  margin-right:auto;
  margin-top:20px;
  color:white;
  display:flex;
  flex-direction:column;
  a{
    width:max-content;
    margin-bottom:20px;
  }
  
  @media screen and (min-width: 960px){
    margin-top:0;
  }
  @media screen and (min-width: 1260px){
    max-width:1220px;
  }
`
const Landing = styled.div`
margin-top:20px;
display:flex;
flex-direction:column;
padding: 20px 40px;
border-bottom: solid #ffffff47 2px;
margin-bottom: 20px;
`
const Links = styled.ul`
  display: flex;
  justify-content: space-between;
  list-style:none;
  .link-icon{
    width:20px;
    color:${theme.textColor};
  }
`
const ProjectInfo = styled.div`
  display:flex;
  flex-direction:column;
  h1{
    font-family:AvenirBlack;
  }
`
const Headline = styled.div`
display:flex;
justify-content:center;
align-items:center;
flex-direction:column;
#context{
    color:${theme.primaryColor};
    font-family: AvenirRoman;
    min-width:fit-content;
    span{
        text-transform:uppercase;
    }
}
h2{
    color: ${theme.titleColor};
    text-transform: uppercase;
    font-family: AvenirBlack;
    letter-spacing: .2rem;
    font-size:36px;
}
`
const Desc = styled.p`
font-family: AvenirBook;
font-size: 18px;
position: relative;
color: ${theme.textColor};
text-align:center;
padding:40px 0;
margin-bottom:20px;
`
const ProjectNav=styled.div`
  padding:0 40px;
`

const Project = styled.div`
img{
  width:100%;
  padding:20px 40px;
  margin-bottom:20px;
}
`

function findPostBySlug(slug) {
    return data.find(o => o.url === slug);
  }

function ProjectDetails() {
    let  {url} = useParams()
    let post = findPostBySlug(url);
    return (
      <PageWrapper>
        <Wrapper>
          <ProjectNav>
            <StyledBtn to="/">Back</StyledBtn>
          </ProjectNav>
          <Landing>
            <ProjectInfo>
              <Headline>
                  <div id="context">
                      <span id="tag">#{post.tag} - </span>
                      <span id="year">{post.year}</span>
                  </div>
                  <h2>{post.title}</h2>
              </Headline>
            <Desc>{post.desc}</Desc>
            </ProjectInfo>
            <Links>
            {post.links.map((item,key)=>{
              return(
                <StyledIconLink key={key}>
                  <a href={item[1]}>
                    <img className="link-icon" src={"../links-icons/"+item[2]} alt={post[0]}></img>
                    {item[0]}
                  </a>
                </StyledIconLink>
              )
            })}
            </Links>
          </Landing>
          <Project>
            {post.project.map((item,key)=>{
              if(item[0]==="img"){
                return(
                  <img key={key}  alt="Project" src={"../post-images/"+item[1]}></img>
                )
              }
              return null;
            })}
          </Project>
        </Wrapper>
      </PageWrapper>
    )
}

export default ProjectDetails
