import React, { Component } from 'react'
import styled from 'styled-components';
import theme from '../../assets/theme.js'

const TransitionContainer=styled.div`
    position:fixed;
    z-index:15;
    width:100vw;
    height:100vh;
    top:0;
    background:black;
    opacity:0;
    pointer-events:none;
    transform-origin: left center ;
    transition: all 500ms;
`

export class TransitionBlock extends Component {
    constructor(props){
        super(props)
        this.domElement=React.createRef()
    }
    render() {
        return (
            <TransitionContainer ref={this.domElement} id="transition">
            </TransitionContainer>
        )
    }
}

export default TransitionBlock
