import React, { Component } from 'react'
import styled from 'styled-components';

export class Cursor extends Component {
    constructor(props){
        super(props)
        this.cursor=React.createRef()
        this.cursorDelay=React.createRef()
    }
    handleMove=(e)=>{
        const x = e.pageX
        const y = e.pageY
        this.cursor.current.setAttribute("style", "top: " + y + "px; left:" + x + "px;")
        setTimeout(()=>{
            this.cursorDelay.current.setAttribute("style", "top: " + y + "px; left:" + x + "px;")
        },
        50)
    }
    componentDidMount(){
        document.addEventListener('mousemove',(e)=>{this.handleMove(e)})
    }
    componentWillUnmount(){
        document.removeEventListener('mousemove',(e)=>{this.handleMove(e)})
    }
    render() {
        const CursorWrapper = styled.div`
        @media screen and (min-width: 960px){
            .cursor{
                position: fixed;
                width: 15px;
                height: 15px;
                border-radius: 100%;
                background: rgba(255,255,255,1);
                position: absolute;
                transform: translate(-50%, -50%);
                pointer-events: none;
                z-index: 100;
            }
            .cursor-delay{
                position: fixed;
                width: 35px;
                height: 35px;
                border-radius: 100%;
                background: rgba(255,255,255,0.1);
                position: absolute;
                transform: translate(-50%, -50%);
                pointer-events: none;
                z-index: 100;
            }
        }
        `
        return (
            <CursorWrapper>
                <div ref={this.cursor} className="cursor"></div>
                <div ref={this.cursorDelay} className="cursor-delay"></div>
            </CursorWrapper> 
        )
    }
}

export default Cursor
